html {
 height: 100%;

 body {
 height: 100%;

 .landing-page-content {
 padding: 50px 0 0 0;
 overflow: hidden;

 .supporting-content {
 padding-top: 20px;
 padding-bottom: 20px;
 }

 .row.supporting-paragraph {
 margin-bottom: 20px;

 .supporting-img {
 max-width: 400px;
 margin-bottom: 20px;
 }

 .float-left.supporting-img {
 margin-right: 20px;
 }

 .float-right.supporting-img {
 margin-left: 20px;
 }
 }
 }

 .section {
 position: relative;
 margin: 40px 0;
 }

 .container-section {
 margin-top: 30px;
 margin-bottom: 30px;
 }

 .landing-full-container {
 height: 100%;

 .landing-full {
 width: 100%;
 height: 100%;
 background-size: cover;
 background-position: center;
 background-attachment: fixed;
 background-repeat: no-repeat;
 position: relative;
 }

 .landing-full::before {
 content: '';
 width: 100%;
 height: 100%;
 position: absolute;
 top: 0;
 left: 0;
 background-color: rgba(0, 0, 0, 0.25);
 }

 .logo-container {
 position: absolute;
 opacity: 0;
 animation-fill-mode: forwards;
 height: 100%;

 .logo {
 margin-bottom: 10px;
 }
 }
 }

 .full-width-carousel-cta {
 position: absolute;
 bottom: 20px;
 width: 100%;
 text-align: center;

 i {
 margin-right: 5px;
 }
 }
 }
}
